import React, { useState, useEffect, useRef } from 'react';
import html2canvas from 'html2canvas';
import { HashRouter as Router } from 'react-router-dom';
import { X, Plus } from 'lucide-react';

function AppContent() {
  const [currentDate, setCurrentDate] = useState(new Date());
  const [now, setNow] = useState(new Date());
  const [entries, setEntries] = useState(() => {
    const saved = localStorage.getItem('calendarData');
    return saved ? JSON.parse(saved) : {};
  });
  const [selectedDate, setSelectedDate] = useState(null);
  const [fullImage, setFullImage] = useState(null);
  const [saveNote, setSaveNote] = useState(""); //    
  const calendarRef = useRef(null);

  const [touchStart, setTouchStart] = useState(null);
  const [touchEnd, setTouchEnd] = useState(null);
  const minSwipeDistance = 50;

  useEffect(() => {
    const timer = setInterval(() => setNow(new Date()), 1000);
    return () => clearInterval(timer);
  }, []);

  useEffect(() => {
    localStorage.setItem('calendarData', JSON.stringify(entries));
  }, [entries]);

  const year = currentDate.getFullYear();
  const month = currentDate.getMonth();
  const firstDayOfMonth = new Date(year, month, 1).getDay();
  const daysInMonth = new Date(year, month + 1, 0).getDate();
  const days = Array.from({ length: daysInMonth }, (_, i) => i + 1);
  const weekDays = ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT'];

  const POINT_COLOR = '#7851C2';
  const NEON_COLOR = '#A388EE';
  const FONT_FAMILY = '"Pretendard Variable", Pretendard, -apple-system, sans-serif';

  const changeMonth = (offset) => setCurrentDate(new Date(year, month + offset, 1));

  const onTouchStart = (e) => {
    setTouchEnd(null);
    setTouchStart(e.targetTouches[0].clientX);
  };
  const onTouchMove = (e) => setTouchEnd(e.targetTouches[0].clientX);
  const onTouchEnd = () => {
    if (!touchStart || !touchEnd) return;
    const distance = touchStart - touchEnd;
    if (distance > minSwipeDistance) changeMonth(1);
    else if (distance < -minSwipeDistance) changeMonth(-1);
  };

  const handleFile = (file, dateKey) => {
    if (file && file.type.startsWith('image/')) {
      const reader = new FileReader();
      reader.onload = (e) => {
        setEntries(prev => {
          const currentPhotos = prev[dateKey]?.photos || [];
          if (currentPhotos.length >= 3) return prev;
          return {
            ...prev,
            [dateKey]: { ...prev[dateKey], photos: [...currentPhotos, e.target.result] }
          };
        });
      };
      reader.readAsDataURL(file);
    }
  };

  const deletePhoto = (dateKey, index) => {
    setEntries(prev => {
      const newPhotos = [...prev[dateKey].photos];
      newPhotos.splice(index, 1);
      return { ...prev, [dateKey]: { ...prev[dateKey], photos: newPhotos } };
    });
  };

  const downloadImage = async () => {
    const note = window.prompt("    . ", "");
    if (note === null) return; //   
    
    setSaveNote(note); //     

    setTimeout(async () => {
      if (calendarRef.current) {
        const canvas = await html2canvas(calendarRef.current, { 
          scale: 3, //   
          useCORS: true,
          backgroundColor: '#ffffff'
        });
        const link = document.createElement('a');
        link.download = `bubble_bin_${year}_${month + 1}.png`;
        link.href = canvas.toDataURL('image/png');
        link.click();
        setSaveNote(""); //    
      }
    }, 100);
  };

  const styles = {
    // 1 & 2.      
    container: { 
      width: '100%', 
      height: '100vh', 
      margin: '0', 
      fontFamily: FONT_FAMILY, 
      backgroundColor: '#fff', 
      color: '#333', 
      overflow: 'hidden', //   
      display: 'flex',
      flexDirection: 'column'
    },
    captureArea: {
      flex: 1,
      backgroundColor: 'white',
      display: 'flex',
      flexDirection: 'column',
      padding: '0'
    },
    header: { padding: '20px', display: 'flex', flexDirection: 'column', gap: '8px' },
    todayInfo: { fontSize: '12px', color: POINT_COLOR, fontWeight: '600', letterSpacing: '0.05em' },
    nav: { display: 'flex', justifyContent: 'space-between', alignItems: 'center' },
    select: { padding: '6px 10px', border: 'none', backgroundColor: '#F3F0F9', borderRadius: '8px', fontSize: '14px', color: POINT_COLOR, fontWeight: 'bold' },
    grid: { display: 'grid', gridTemplateColumns: 'repeat(7, 1fr)', gap: '1px', backgroundColor: '#eee', borderTop: '1px solid #eee', borderBottom: '1px solid #eee' },
    cell: (isToday) => ({
      aspectRatio: '1/1.6', //    
      backgroundColor: 'white',
      position: 'relative',
      overflow: 'hidden',
      boxShadow: isToday ? `inset 0 0 0 1.5px ${POINT_COLOR}, inset 0 0 12px ${NEON_COLOR}88` : 'none',
      zIndex: isToday ? 2 : 1
    }),
    dayNum: { position: 'absolute', top: '6px', left: '6px', fontSize: '11px', fontWeight: '700', zIndex: 2 },
    saveNoteArea: {
      padding: '30px 20px',
      textAlign: 'center',
      fontSize: '15px',
      color: '#555',
      fontStyle: 'italic',
      minHeight: '80px',
      lineHeight: '1.6',
      wordBreak: 'break-all'
    },
    modal: { position: 'fixed', inset: 0, backgroundColor: 'rgba(0,0,0,0.7)', display: 'flex', alignItems: 'center', justifyContent: 'center', zIndex: 100, padding: '20px', backdropFilter: 'blur(3px)' },
    modalContent: { backgroundColor: 'white', padding: '24px', borderRadius: '32px', width: '100%', maxWidth: '340px', position: 'relative' },
    closeBtn: { position: 'absolute', top: '20px', right: '20px', border: 'none', background: 'none', color: '#999' },
    photoScroll: (isFull) => ({ display: 'flex', gap: '10px', overflowX: 'auto', paddingBottom: '10px', scrollSnapType: isFull ? 'x mandatory' : 'none' }),
    photoItem: { flex: '0 0 85%', aspectRatio: '1/1', borderRadius: '15px', overflow: 'hidden', position: 'relative' },
    smallAddBtn: { flex: '0 0 45%', aspectRatio: '1/1', borderRadius: '15px', backgroundColor: '#F3F0F9', border: `1px dashed ${POINT_COLOR}`, display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' },
    bigAddBtn: { width: '100%', aspectRatio: '16/9', borderRadius: '15px', backgroundColor: '#F3F0F9', border: `1px dashed ${POINT_COLOR}`, display: 'flex', alignItems: 'center', justifyContent: 'center' },
    textarea: { width: '100%', height: '100px', border: '1px solid #eee', borderRadius: '15px', padding: '12px', fontSize: '15px', marginTop: '15px', resize: 'none', boxSizing: 'border-box' }
  };

  return (
    <div style={styles.container}>
      {/* 3.    calendarRef    */}
      <div ref={calendarRef} style={styles.captureArea} onTouchStart={onTouchStart} onTouchMove={onTouchMove} onTouchEnd={onTouchEnd}>
        <header style={styles.header}>
          <div style={styles.todayInfo}>
            {now.toLocaleDateString('ko-KR')} {now.toLocaleTimeString('ko-KR', { hour: '2-digit', minute: '2-digit', hour12: false })}
          </div>
          <div style={styles.nav}>
            <div style={{display: 'flex', gap: '8px'}}>
              <select value={year} onChange={(e) => setCurrentDate(new Date(parseInt(e.target.value), month))} style={styles.select}>
                {[2025, 2026, 2027, 2028, 2029, 2030].map(y => <option key={y} value={y}>{y}</option>)}
              </select>
              <select value={month} onChange={(e) => setCurrentDate(new Date(year, parseInt(e.target.value)))} style={styles.select}>
                {Array.from({length: 12}, (_, i) => <option key={i} value={i}>{i+1}</option>)}
              </select>
            </div>
            <button onClick={downloadImage} style={{backgroundColor: POINT_COLOR, color: '#fff', padding: '8px 16px', borderRadius: '20px', border: 'none', fontWeight: 'bold', fontSize: '13px'}}>SAVE</button>
          </div>
        </header>

        <div style={styles.grid}>
          {weekDays.map((wd, i) => (
            <div key={wd} style={{textAlign: 'center', fontSize: '10px', padding: '8px 0', color: i===0?'#ff6b6b':i===6?'#4dabf7':'#adb5bd', fontWeight: '800', backgroundColor: 'white'}}>{wd}</div>
          ))}
          {firstDayOfMonth > 0 && Array.from({length: firstDayOfMonth}).map((_, i) => <div key={`empty-${i}`} style={{backgroundColor: '#F8F9FA'}} />)}
          {days.map(day => {
            const dateKey = `${year}-${month + 1}-${day}`;
            const photos = entries[dateKey]?.photos || [];
            const isToday = year === now.getFullYear() && month === now.getMonth() && day === now.getDate();
            return (
              <div key={day} style={styles.cell(isToday)} onClick={() => setSelectedDate(day)}>
                <span style={{...styles.dayNum, color: photos.length > 0 ? 'white' : '#333', textShadow: photos.length > 0 ? '0 1px 4px rgba(0,0,0,0.5)' : 'none'}}>{day}</span>
                {photos.length > 0 && <img src={photos[0]} style={{width: '100%', height: '100%', objectFit: 'cover', display: 'block'}} alt="" />}
              </div>
            );
          })}
        </div>

        {/* 4. SAVE     */}
        <div style={styles.saveNoteArea}>
          {saveNote ? `"${saveNote}"` : ""}
        </div>
      </div>

      {/*      */}
      {selectedDate && (() => {
        const dateKey = `${year}-${month + 1}-${selectedDate}`;
        const entry = entries[dateKey] || { photos: [], memo: '' };
        return (
          <div style={styles.modal} onClick={() => setSelectedDate(null)}>
            <div style={styles.modalContent} onClick={e => e.stopPropagation()}>
              <button style={styles.closeBtn} onClick={() => setSelectedDate(null)}><X size={24} /></button>
              <h2 style={{fontSize: '18px', fontWeight: '800', marginBottom: '20px', color: POINT_COLOR}}>{month + 1} {selectedDate}</h2>
              {entry.photos && entry.photos.length > 0 ? (
                <div style={styles.photoScroll(entry.photos.length === 3)}>
                  {entry.photos.map((p, i) => (
                    <div key={i} style={styles.photoItem}>
                      <img src={p} style={{width: '100%', height: '100%', objectFit: 'cover'}} onClick={() => setFullImage(p)} alt="" />
                      <button onClick={() => deletePhoto(dateKey, i)} style={{position: 'absolute', top: '8px', right: '8px', background: 'rgba(0,0,0,0.5)', color: 'white', border: 'none', borderRadius: '50%', width: '24px', height: '24px', display: 'flex', alignItems: 'center', justifyContent: 'center'}}></button>
                    </div>
                  ))}
                  {entry.photos.length < 3 && (
                    <div style={styles.smallAddBtn} onClick={() => document.getElementById('fileInput').click()}>
                      <Plus size={20} color={POINT_COLOR} />
                      <span style={{fontSize: '11px', color: POINT_COLOR}}></span>
                    </div>
                  )}
                </div>
              ) : (
                <div style={styles.bigAddBtn} onClick={() => document.getElementById('fileInput').click()}>
                  <Plus size={24} color={POINT_COLOR} /><span style={{fontSize: '14px', color: POINT_COLOR, marginLeft: '8px'}}> </span>
                </div>
              )}
              <input id="fileInput" type="file" accept="image/*" onChange={(e) => handleFile(e.target.files[0], dateKey)} style={{display: 'none'}} />
              <textarea style={styles.textarea} placeholder="  ." value={entry.memo} onChange={(e) => setEntries(prev => ({...prev, [dateKey]: { ...prev[dateKey], memo: e.target.value }}))} />
              <div style={{display: 'flex', gap: '8px', marginTop: '20px'}}>
                <button onClick={() => { if(window.confirm('?')) { const n={...entries}; delete n[dateKey]; setEntries(n); setSelectedDate(null); }}} style={{flex: 1, padding: '14px', borderRadius: '16px', border: '1px solid #eee', color: '#999'}}></button>
                <button onClick={() => setSelectedDate(null)} style={{flex: 2, padding: '14px', borderRadius: '16px', border: 'none', backgroundColor: POINT_COLOR, color: 'white', fontWeight: 'bold'}}></button>
              </div>
            </div>
          </div>
        );
      })()}

      {fullImage && (
        <div style={{...styles.modal, backgroundColor: 'rgba(0,0,0,0.9)'}} onClick={() => setFullImage(null)}>
          <img src={fullImage} style={{maxWidth: '100%', maxHeight: '80%', objectFit: 'contain'}} alt="full" />
        </div>
      )}
    </div>
  );
}

function App() { return <Router><AppContent /></Router>; }
export default App;